import { BiPlugin } from '../../types';
import * as actionsOld from './actions';
import { actions } from './actions';
import { BiPluginConfig } from '../../types';
import createOnAction from './createOnAction';
import createRenderEditable from './createRenderEditable';
import createQueries from './createQueries';

function createHoverBlockPlugin(config: BiPluginConfig): BiPlugin {
  const hoverBlockConfig = config.hoverBlock || {};
  return {
    actions,
    onAction: createOnAction(hoverBlockConfig),
    renderEditable: createRenderEditable(hoverBlockConfig),
    queries: createQueries(hoverBlockConfig),
  };
}

createHoverBlockPlugin.actions = actionsOld;

export default createHoverBlockPlugin;
